import type { TopicGroup } from '../views/vocabulary/types'

export interface GroupedVocabulary {
  date: string
  displayDate: string
  vocabularies: any[]
  topics?: TopicGroup[]
  totalItems?: number
  currentPage?: number
  totalPages?: number
}

/**
 * Format date to readable string
 */
export const formatDate = (dateString: string | undefined, locale: string = 'vi-VN'): string => {
  if (!dateString) return ''
  
  try {
    const date = new Date(dateString)
    return date.toLocaleDateString(locale, {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  } catch (error) {
    return ''
  }
}

/**
 * Format date and time to readable string
 */
export const formatDateTime = (dateString: string | undefined, locale: string = 'vi-VN'): string => {
  if (!dateString) return ''
  
  try {
    const date = new Date(dateString)
    return date.toLocaleString(locale, {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  } catch (error) {
    return ''
  }
}

/**
 * Get relative time from now (e.g., "2 hours ago", "yesterday", "3 days ago")
 */
export const getRelativeTime = (dateString: string | undefined, t?: (key: string, params?: any) => string): string => {
  if (!dateString) return ''
  
  try {
    const date = new Date(dateString)
    const now = new Date()
    const diffInMs = now.getTime() - date.getTime()
    const diffInHours = Math.floor(diffInMs / (1000 * 60 * 60))
    const diffInDays = Math.floor(diffInHours / 24)
    
    if (diffInHours < 1) {
      const diffInMinutes = Math.floor(diffInMs / (1000 * 60))
      if (diffInMinutes < 1) {
        return t ? t('time.ago.justNow') : 'Just now'
      }
      return t ? t('time.ago.minutesAgo', { minutes: diffInMinutes }) : `${diffInMinutes} minutes ago`
    } else if (diffInHours < 24) {
      return t ? t('time.ago.hoursAgo', { hours: diffInHours }) : `${diffInHours} hours ago`
    } else if (diffInDays === 1) {
      return t ? t('time.ago.yesterday') : 'Yesterday'
    } else if (diffInDays < 7) {
      return t ? t('time.ago.daysAgo', { days: diffInDays }) : `${diffInDays} days ago`
    } else {
      return formatDate(dateString)
    }
  } catch (error) {
    return ''
  }
}

/**
 * Get date key for grouping (YYYY-MM-DD)
 */
export const getDateKey = (dateString: string | undefined): string => {
  if (!dateString) return ''
  
  try {
    const date = new Date(dateString)
    return date.toISOString().split('T')[0]
  } catch (error) {
    return ''
  }
}

/**
 * Group vocabularies by topic
 */
export const groupVocabulariesByTopic = (vocabularies: any[]): TopicGroup[] => {
  const grouped = new Map<string, any[]>()
  const uncategorizedKey = 'Uncategorized'

  vocabularies.forEach(vocab => {
    const topic = vocab.category || uncategorizedKey
    if (!grouped.has(topic)) {
      grouped.set(topic, [])
    }
    grouped.get(topic)!.push(vocab)
  })

  const result = Array.from(grouped.entries()).map(([topic, vocabs]) => {
    // Get category name from the first vocabulary item in the group (for imported data)
    const firstVocab = vocabs[0]
    // If there's a categoryName from imported data, use it
    // Otherwise, we'll let the component use getTopicName to get the proper translation
    const categoryName = firstVocab?.categoryName || null
    
    return {
      topic,
      categoryName, // Preserve category name from imported data
      vocabularies: vocabs.sort((a, b) => {
          const timeA = new Date(a.createdAt || '').getTime()
          const timeB = new Date(b.createdAt || '').getTime()
          return timeB - timeA
        })
    }
  })

  return result.sort((a, b) => {
    if (a.topic === uncategorizedKey) return 1;
    if (b.topic === uncategorizedKey) return -1;
    return a.topic.localeCompare(b.topic);
  });
}

/**
 * Group vocabularies by date and then by topic
 */
export const groupVocabulariesByDateAndTopic = (
  vocabularies: any[],
  locale: string = 'vi-VN',
  t?: (key: string) => string
): GroupedVocabulary[] => {
  const groupedByDate = groupVocabulariesByDate(vocabularies, locale, t)

  return groupedByDate.map(dateGroup => {
    return {
      ...dateGroup,
      topics: groupVocabulariesByTopic(dateGroup.vocabularies)
    }
  })
}

/**
 * Group vocabularies by date
 */
export const groupVocabulariesByDate = (
  vocabularies: any[],
  locale: string = 'vi-VN',
  t?: (key: string) => string
): GroupedVocabulary[] => {
  const grouped = new Map<string, any[]>()
  
  // Group by date key
  vocabularies.forEach(vocab => {
    const dateKey = getDateKey(vocab.createdAt)
    if (!dateKey) return
    
    if (!grouped.has(dateKey)) {
      grouped.set(dateKey, [])
    }
    grouped.get(dateKey)!.push(vocab)
  })
  
  // Convert to array and sort by date (newest first)
  const result: GroupedVocabulary[] = Array.from(grouped.entries())
    .map(([dateKey, vocabs]) => ({
      date: dateKey,
      displayDate: getGroupDisplayDate(dateKey, locale, t),
      vocabularies: vocabs.sort((a, b) => {
        // Sort by creation time within the same date (newest first)
        const timeA = new Date(a.createdAt || '').getTime()
        const timeB = new Date(b.createdAt || '').getTime()
        return timeB - timeA
      })
    }))
    .sort((a, b) => {
      // Sort groups by date (newest first)
      return b.date.localeCompare(a.date)
    })
  
  return result
}

/**
 * Get display name for date group
 */
export const getGroupDisplayDate = (dateKey: string, locale: string = 'vi-VN', t?: (key: string) => string): string => {
  try {
    const date = new Date(dateKey + 'T00:00:00')
    const today = new Date()
    const yesterday = new Date(today)
    yesterday.setDate(today.getDate() - 1)
    
    const todayKey = getDateKey(today.toISOString())
    const yesterdayKey = getDateKey(yesterday.toISOString())
    
    if (dateKey === todayKey) {
      // Prefer i18n if provided
      if (t) return t('time.today') || 'Today'
      // Fallback by locale (support vi and ko)
      const l = locale.toLowerCase()
      if (l.startsWith('vi')) return 'Hôm nay'
      if (l.startsWith('ko')) return '오늘'
      return 'Today'
    } else if (dateKey === yesterdayKey) {
      if (t) return t('time.yesterday') || 'Yesterday'
      const l = locale.toLowerCase()
      if (l.startsWith('vi')) return 'Hôm qua'
      if (l.startsWith('ko')) return '어제'
      return 'Yesterday'
    } else {
      return formatDate(date.toISOString(), locale)
    }
  } catch (error) {
    return dateKey
  }
}
